import React from 'react';
import { NavLink } from 'react-router-dom';

const styles = {
  link: {
    display: 'inline-block',
    textDecoration: 'none',
    padding: 12,
    fontWeight: 700,
    color: '#2A363B',
    fontSize: 20,
    fontFamily: "Roboto, sans-serif"
  },
  activeLink: {
    color: '#E84A5F',
  },
};

const AuthNav = () => (
  <div>
    <NavLink
      to='/register'
      exact
      style={styles.link}
      activeStyle={styles.activeLink}
    >
      Register
    </NavLink>
    <NavLink
      to='/login'
      exact
      style={styles.link}
      activeStyle={styles.activeLink}
    >
      Login
    </NavLink>
  </div>
);

export default AuthNav;